import clsx from 'clsx';
import { FunctionalIcon } from '../icon/Icon';
import { DropDownItem, DropDownProps } from './DropDown.interface';
import { Listbox, Transition } from '@headlessui/react';
import React, { Fragment } from 'react';
import { getDistanceToBottom } from '../../../utils/getDistanceToBottom';
import { twMerge } from 'tailwind-merge';

const MENU_HEIGHT = 268;

export const DropDown: React.FC<DropDownProps> = ({
  disabled = false,
  items,
  message,
  label,
  placeholder,
  readonly = false,
  state = 'default',
  ...props
}) => {
  const [selectedItem, setSelectedItem] = React.useState<DropDownItem>();
  const menuRef = React.useRef(null);
  const buttonRef = React.useRef(null);
  const hasError = state === 'error';
  const iconColor = hasError ? 'text-zep-error' : state === 'info' ? 'text-zep-info' : 'text-zep-typography-dark-100';
  const disabledOrReadonly = disabled || readonly;

  return (
    <div
      className={twMerge(clsx('relative', 'inline-block', 'text-left'), props.className)}
      data-testid="zep-drop-down"
    >
      <Listbox disabled={disabledOrReadonly} value={selectedItem} onChange={setSelectedItem}>
        {({ open }) => {
          const distanceToBottom = getDistanceToBottom(buttonRef.current);
          const openOnTop = distanceToBottom < MENU_HEIGHT;
          const displayValue = selectedItem?.name ?? placeholder;
          const color = disabled
            ? 'text-zep-typography-dark-70/40'
            : !selectedItem?.name && !readonly
            ? 'text-zep-typography-dark-70/70'
            : 'text-zep-typography-dark-1ß0/1ß0';

          const buttonClasses = clsx(
            'inline-flex',
            'justify-between',
            'rounded',
            'border-solid',
            'pl-4',
            'pr-3',
            'py-3',
            'zep-body-text',
            'w-[326px]',
            'outline-none',
            hasError ? 'border-2' : 'border',
            color,
            readonly ? 'bg-gray-100' : 'bg-white',
            { ring: open },
            { 'border-zep-typography-dark-70': !disabled && !hasError },
            { 'border-zep-typography-dark-70/40': disabled },
            { 'border-zep-error': hasError },
            { 'active:ring': !disabledOrReadonly },
            { 'ring-offset-1': !disabledOrReadonly },
            { 'focus-visible:ring': !disabledOrReadonly },
            { 'ring-zep-focus': !disabledOrReadonly },
            { 'cursor-not-allowed': disabledOrReadonly },
          );

          const menuClasses = clsx(
            'zep-drop-down-menu',
            'absolute',
            'right-0',
            'z-10',
            'py-1',
            openOnTop ? 'mb-[-25px]' : 'mt-1',
            'w-[326px]',
            'max-h-[268px]',
            'origin-top-right',
            'rounded-md',
            'bg-white',
            'drop-shadow-md',
            'ring-1,',
            'ring-black',
            'ring-opacity-5',
            'focus:outline-none',
            'overflow-hidden',
            'overflow-y-auto',
            { 'bottom-full': openOnTop },
          );

          const chevronClasses = clsx(
            disabledOrReadonly ? 'text-zep-typography-dark-70/40' : 'text-zep-typography-dark-70/100',
            'transition-transform',
            'duration-500',
            'shrink-0',
            {
              'rotate-180': open,
            },
          );

          const getMenuItemClasses = (hovered: boolean, isActive) => {
            return clsx(
              'px-4 py-3 text-md zep-body-text flex w-full justify-between items-center',
              hovered ? 'bg-gray-100 text-gray-900' : 'text-zep-typography-dark-70',
              { 'bg-gray-200': isActive },
              { 'pr-12': isActive },
            );
          };

          return (
            <>
              {label && (
                <Listbox.Label className={clsx('block', 'mb-zep-0.5', { 'text-zep-typography-dark-70/40': disabled })}>
                  {label}
                </Listbox.Label>
              )}

              <Listbox.Button
                className={buttonClasses}
                ref={buttonRef}
                title={displayValue}
                data-testid="zep-drop-down-button"
              >
                <>
                  <span className="truncate">{displayValue}</span>
                  <FunctionalIcon className={chevronClasses} name="ChevronMiniDown" role="img" size={24} />
                </>
              </Listbox.Button>
              <Transition
                as={Fragment}
                enter="transition ease-out duration-100"
                enterFrom="transform opacity-0 scale-95"
                enterTo="transform opacity-100 scale-100"
                leave="transition ease-in duration-25"
                leaveFrom="transform opacity-100 scale-100"
                leaveTo="transform opacity-0 scale-95"
              >
                <Listbox.Options className={menuClasses} ref={menuRef} data-testid="zep-drop-down-menu">
                  {items.map((item, i) => (
                    <Listbox.Option
                      key={item.id}
                      value={item}
                      title={item.name}
                      className={({ active, selected }) => getMenuItemClasses(active, selected)}
                    >
                      {({ selected }) => {
                        return (
                          <>
                            <span className="truncate">{item.name}</span>
                            {selected && (
                              <FunctionalIcon
                                name="Check"
                                role="img"
                                size={24}
                                className="text-zep-green-50 absolute right-4"
                              />
                            )}
                          </>
                        );
                      }}
                    </Listbox.Option>
                  ))}
                </Listbox.Options>
              </Transition>
            </>
          );
        }}
      </Listbox>
      {message && (
        <div className={clsx('mt-[6px]', 'zep-tagline', 'flex', iconColor)}>
          {state !== 'default' && <FunctionalIcon name="Info" role="img" size={24} className="mr-2" />}
          <span>{message}</span>
        </div>
      )}
    </div>
  );
};
